import {ctx} from '../index'

let canvasSize = 128;

function swapSize(size){
    canvasSize = size;
    canvas.height = size;
    canvas.width = size;
    makeActiveSize(size);
    const dataURL = localStorage.getItem('canvas');
    const img = new Image();
    img.crossOrigin = 'Anonymous';
    img.src = dataURL;
    img.onload = function load() {
      ctx.drawImage(img, 0, 0, 512 / (512 / canvasSize), 512 / (512 / canvasSize));
    };
  }

  function makeActiveSize(size){
    document.getElementById('size32').classList.remove('active');
    document.getElementById('size64').classList.remove('active');
    document.getElementById('size128').classList.remove('active');
    document.getElementById(`size${size}`).classList.add('active');
  }
  export { swapSize,canvasSize,makeActiveSize }